  },
  methods:{
      gotoClick(){
        this.$router.push({name:'PageContent2'})
      }
    
  },
  mounted(){
    setTimeout(()=>{
      this.showTip=false
    },5000)

for(let i=0;i< this.imgs.length;i++){
        let img=this.imgs[i]
        img.startY-=this.marginTop
        img.endY-=this.marginTop
        img.top=img.originTop
        }

window.scrollTo(0,0)
  var path = this.$refs['svg_4'];
  var $car = $(this.$refs['car']);

  var timer = null;
  // 最重要的两个属性  获取长度，以及每个点的坐标
  var totalLength = path.getTotalLength();
  var initPosition = path.getPointAtLength(0);
  var prePosition = initPosition;
  var curPosition = initPosition;

  // 获取前进切线角度
  function getRotate(a, b) {
    var k = (b.y - a.y) / (b.x - a.x);
    var rotate = Math.atan(k) * 180 / Math.PI;
    return k < 0 ? rotate + 90 : rotate - 90;
  }

  // 定位car 
  $car.css({
    left: initPosition.x,
    top: initPosition.y,
    //'-webkit-transform': 'translate(-2px, 0) rotate(' + getRotate(curPosition, path.getPointAtLength(1)) + 'deg)'
  }).show()

 
 var t=this

 window.requestAnimationFrame=window.requestAnimationFrame||function(fn){
   setTimeout(()=>{
     fn()
   },10)
 }

 function render(){
   let scrollY=window.pageYOffset||window.scrollY||document.documentElement.scrollTop
      curPosition = path.getPointAtLength(totalLength / (t.heigh-window.innerHeight-t.carTop) *scrollY );

      $car.css({
        left: curPosition.x,
        top: curPosition.y,
        //'-webkit-transform': 'translate(-2px, 0) rotate(' + getRotate(prePosition, curPosition) + 'deg)'
      }).show();
 
      //$car.css({"-webkit-transform":'translate('+curPosition.x+'px,'+ curPosition.y+'px)'}).show()
      prePosition = curPosition;

      t.showGotoButton=(t.heigh-scrollY)<=1500;
      // 
      for(let i=0;i< t.imgs.length;i++){
        let img=t.imgs[i]
        
        if( scrollY<=img.endY){
          img.top=img.originTop- (scrollY-img.startY)/(img.endY-img.startY)*(img.originTop-img.maxTop)
          img.opacity=(scrollY-img.startY)/(img.endY-img.startY)
        } else{
          img.top=img.maxTop
          img.opacity=1
        }
      
      }

      requestAnimationFrame(render)
 }
 render()

