<template>
  <!--最新，底部黑洞-->
  <div class="container" :style="{height:pageHeight+'px'}">
    <div class="page page-current">
      <div ref="car" class="car" style="position:absolute;">
        <img src="@/assets/images/page1/骆驼.png">
      </div>
      <img ref="bg" :src="bgImage" class="bg">
      <svg
        width="100%"
        :height="pageHeight"
        xmlns="http://www.w3.org/2000/svg"
        xmlns:xlink="http://www.w3.org/1999/xlink"
      >
        <!-- Created with Method Draw - http://github.com/duopixel/Method-Draw/ -->
        <g>
          <path
            id="path"
            ref="svg_4"
            d="m321.81818,265.45446c0,0 185.45455,30.90909 214.54546,167.27273c5.45455,109.0909 -260,163.63636 -285.45455,201.81818c-61.81818,21.81818 -134.54545,107.27272 -134.54545,107.27272c-43.63636,63.63636 -50.90909,123.63637 -50.90909,123.63637c0,0 10.90909,100 10.90909,100c0,0 34.54545,98.18182 34.54545,98.18182c0,0 90.90909,100 90.90909,100c0,0 145.45455,116.36363 145.45455,116.36363c0,0 85.45454,80 85.45454,80c0,0 43.63637,43.63637 43.63637,43.63637c0,0 47.27272,65.45454 47.27272,65.45454c0,0 40,74.54546 40,74.54546c0,0 10.90909,132.72727 10.90909,132.72727c-16.36364,94.54545 -60,143.63636 -60,143.63636c-54.54545,52.72727 -269.09091,174.54546 -343.63636,232.72728c-38.18182,36.36364 -81.81818,130.90909 -81.81818,129.0909c0,-1.81818 -16.36364,103.63637 -16.36364,103.63637c0,0 5.45455,81.81818 5.45455,81.81818c0,0 38.18182,70.90909 38.18182,70.90909c0,0 69.09091,52.72727 69.09091,52.72727c0,0 214.54545,83.63637 214.54545,83.63637c0,0 83.63636,45.45454 83.63636,45.45454c0,0 47.27273,52.72728 47.27273,52.72728c0,0 25.45455,78.18181 25.45455,78.18181c0,0 -3.63637,52.72728 -3.63637,52.72728c0,0 -21.81818,85.45454 -21.81818,85.45454c0,0 -90.90909,107.27273 -90.90909,107.27273c0,0 -250.90909,185.45454 -250.90909,185.45454c0,0 -89.09091,121.81819 -89.09091,121.81819c0,0 -30.90909,105.45454 -30.90909,105.45454c0,0 -3.63636,90.90909 -3.63636,90.90909c0,0 23.63636,74.54546 23.63636,74.54546c0,0 45.45454,85.45454 45.45454,85.45454c0,0 65.45455,63.63637 65.45455,63.63637c0,0 50.90909,27.27272 50.90909,27.27272c0,0 43.63636,23.63637 43.63636,23.63637"
            opacity="0.5"
            fill-opacity="null"
            stroke-opacity="null"
            stroke-width="0"
            fill="none"
          ></path>
        </g>
      </svg>
      <div
        class="duihua"
        ref="duihuacontainer"
        v-for="(img,index) in imgs"
        :key="index"
        :style="{top:(img.startY+offsetY)+'px',left:img.left+'px'}"
      >
        <img :src="img.path" :alt="index" ref="duihua" :style="{opacity:(img.opacity||0)}">
      </div>
      <!-- <img class="duihua" :src="img.path" v-for="img in imgs" :key="img.id" :style="{top:img.top+'px',left:img.left+'px',opacity:img.opacity}"  /> -->

      <div class="bottom" v-if="isScrollEnd&& nextPageUrl!=null">
        <button @click="gotoClick">点击黑洞</button>
      </div>
      <transition name="fade">
        <div class="tip" v-if="showTip">
          <img src="@/assets/AjOMa3pH.gif">
        </div>
      </transition>
      <!-- <div style="position:fixed;top:0;left:0">{{pathLength}}</div> -->
    </div>
  </div>
</template>

<script>
// import PageConfig from "@/assets/js/PageConfig.js";
export default {
  data() {
    return {
      isScrollEnd: false,
      pathLength:0,
      pageConfig: [
        {
          pageHeight: 3937,
          showY: 670,
          offsetY: 100, //从开始位置偏移距离
          speed: 20, //滚轮转动一格移动速度
          showTip: true,
          bgImage: require("@/assets/images/page1/背景.png"),
          nextPageUrl: "/",
          imgs: [
            {
              path: require("@/assets/images/page1/话术1.png"),
              startY: 430, //开始位置
              left: 180, //距离左侧位置
              eventLength: 120
            },
            {
              path: require("@/assets/images/page1/话术2.png"),
              startY: 900, //开始位置
              left: 230, //距离左侧位置
              eventLength: 438
            },
            {
              path: require("@/assets/images/page1/话术3.png"),
              startY: 1035, //开始位置
              left: 170, //距离左侧位置
              eventLength: 657
            },
            {
              path: require("@/assets/images/page1/话术4.png"),
              startY: 1525, //开始位置
              left: 180, //距离左侧位置
              eventLength: 1368
            },
            {
              path: require("@/assets/images/page1/话术5.png"),
              startY: 1615, //开始位置
              left: 180, //距离左侧位置
              eventLength: 1556
            },
            {
              path: require("@/assets/images/page1/话术6.png"),
              startY: 1750, //开始位置
              left: 180, //距离左侧位置
              eventLength: 1744
            },
            {
              path: require("@/assets/images/page1/话术7.png"),
              startY: 2240, //开始位置
              left: 190, //距离左侧位置
              eventLength: 2498
            },
            {
              path: require("@/assets/images/page1/话术8.png"),
              startY: 2325, //开始位置
              left: 240, //距离左侧位置
              eventLength: 2498
            },
            {
              path: require("@/assets/images/page1/话术9.png"),
              startY: 2750, //开始位置
              left: 170, //距离左侧位置
              eventLength: 3400
            },
            {
              path: require("@/assets/images/page1/话术10.png"),
              startY: 3330, //开始位置
              left: 170, //距离左侧位置
              eventLength: 4227
            },
            {
              path: require("@/assets/images/page1/话术11.png"),
              startY: 3430, //开始位置
              left: 300, //距离左侧位置
              eventLength: 4519
            },
            {
              path: require("@/assets/images/page1/话术12.png"),
              startY: 3550, //开始位置
              left: 300, //距离左侧位置
              eventLength: 4600
            }
          ]
        }
      ],
      id: 0
    };
  },
  computed: {
    pageHeight() {
      return this.pageConfig[this.id].pageHeight;
    },
    showY() {
      return this.pageConfig[this.id].showY || window.innerHeight / 2;
    },
    offsetY() {
      return this.pageConfig[this.id].offsetY || 100; //从开始位置偏移距离
    },
    speed() {
      return this.pageConfig[this.id].speed || 20; //滚轮转动一格移动速度
    },
    showTip() {
      return this.pageConfig[this.id].showTip;
    },
    nextPageUrl() {
      return this.pageConfig[this.id].nextPageUrl;
    },
    bgImage() {
      return this.pageConfig[this.id].bgImage;
    },
    imgs() {
      return this.pageConfig[this.id].imgs;
    }
  },
  watch: {
    $route(to, from) {
      //console.log(this.getStatus(this.$route.path))
    }
  },
  methods: {
    gotoClick() {
      this.$router.push(this.nextPageUrl);
    },
    onScrollEnd(isEnd) {
      this.isScrollEnd = isEnd;
      console.log(`this.isScrollEnd=${this.isScrollEnd}`);
    }
  },
  mounted() {
    let hideTip = setTimeout(() => {
      this.pageConfig[this.id].showTip = false;
    }, 5000);

    $(window).one("scroll", () => {
      this.pageConfig[this.id].showTip = false;
      if (hideTip) {
        clearTimeout(hideTip);
      }
    });

    $(window).on("scroll", () => {
      if (
        Math.abs(window.scrollY + window.innerHeight - $(document).height()) <=
        3
      ) {
        this.onScrollEnd(true);
      } else {
        this.onScrollEnd(false);
      }
    });

    this.id = this.$route.params.id || 0;

    window.scrollTo(0, 0);
    var path = this.$refs["svg_4"];
    var $car = $(this.$refs["car"]);

    var timer = null;
    // 最重要的两个属性  获取长度，以及每个点的坐标
    var totalLength = path.getTotalLength();
    var initPosition = path.getPointAtLength(0);
    var lastPosition = path.getPointAtLength(totalLength);
    var prePosition = initPosition;
    var curPosition = initPosition;
    console.log(totalLength);
    // 获取前进切线角度
    function getRotate(a, b) {
      var k = (b.y - a.y) / (b.x - a.x);
      var rotate = (Math.atan(k) * 180) / Math.PI;
      return k < 0 ? rotate + 90 : rotate - 90;
    }

    // 定位car
    $car
      .css({
        left: initPosition.x,
        top: initPosition.y
        //'-webkit-transform': 'translate(-2px, 0) rotate(' + getRotate(curPosition, path.getPointAtLength(1)) + 'deg)'
      })
      .show();

    var t = this;

    window.requestAnimationFrame =
      window.requestAnimationFrame ||
      function(fn) {
        setTimeout(() => {
          fn();
        }, 10);
      };

    function render() {
      let scrollY =
        window.pageYOffset ||
        window.scrollY ||
        document.documentElement.scrollTop;

      var moveLength =
        (totalLength / ($(document).height() - window.innerHeight)) * scrollY;
      if (moveLength > totalLength) {
        moveLength = totalLength;
      }
      t.pathLength =Math.floor( moveLength);
      //
      curPosition = path.getPointAtLength(moveLength);
      $car
        .css({
          left: curPosition.x,
          top: curPosition.y
          //'-webkit-transform': 'translate(-2px, 0) rotate(' + getRotate(prePosition, curPosition) + 'deg)'
        })
        .show();

      //$car.css({"-webkit-transform":'translate('+curPosition.x+'px,'+ curPosition.y+'px)'}).show()
      prePosition = curPosition;
      // console.log(curPosition)
      //console.log('length='+(totalLength / ($(document).height()- window.innerHeight)) * scrollY)
      if (t.imgs.length) {
        for (var i = 0; i < t.$refs["duihuacontainer"].length; i++) {
          let container = t.$refs["duihuacontainer"][i];
          let img = t.$refs["duihua"][i];
          let imgData = t.imgs[i];
          // let containerRect = container.getBoundingClientRect();
          // console.log(containerRect)
          let top = parseFloat($(img).css("top"));
          let opacity = parseFloat($(img).css("opacity"));
          if (imgData.eventLength <= t.pathLength) {
            top -= t.speed;
            opacity += t.speed / t.offsetY;
            if (top < -t.offsetY) {
              top = -t.offsetY;
            }
            if (opacity > 1) {
              opacity = 1;
            }
          } else {
            top += t.speed;
            opacity -= t.speed / t.offsetY;
            if (top > 0) {
              top = 0;
            }
            if (opacity < 0) {
              opacity = 0;
            }
          }

          $(img).css({ top: top + "px", opacity: opacity });
        }
      }

      requestAnimationFrame(render);
    }
    render();
  }
};
</script>


<style scoped>
.bg {
  position: absolute;
  top: 0;
  width: 100%;
}
path {
  stroke-dasharray: 3000;
  stroke-dashoffset: 3000;
  -webkit-animation: dash 5s linear infinite;
  animation: dash 5s linear infinite;
}

.container {
  position: absolute;
  width: 100%;
  overflow: hidden;
}

.container .page .duihua {
  position: absolute;
  width: 100%;
}
.container .page .duihua img {
  position: relative;
}

.car {
  z-index: 999;
}
.car img {
  position: relative;
  left: -40px;
  top: -40px;
}
.bottom {
  width: 100%;
  position: absolute;
  text-align: center;
  bottom: 100px;
}
.bottom button {
  font-size: 0.5rem;
  animation-name: flash;
  animation-duration: 1s;
  animation-timing-function:ease-in-out;
  animation-direction:alternate;
  animation-iteration-count: infinite
}
.tip {
  position: fixed;
  width: 100%;
  bottom: 5px;
  text-align: center;
}

@keyframes flash {
  from{
    opacity: 0.3;
  }
  to{opacity: 1;}
}
</style>



